
.bg1 {
  position: relative;
  width: 50vw;
  height: 100vh;
  left: 14vw;
  top: 0px;
  background: #E3E7DF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
}

.bg2 {
  position: absolute;
  width: 50vw;
  height: 100vh;
  left: 17vw;
  top: 0px;
  background: #F9F5F2;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
}

.main_ground {
  position: fixed;
  width: 100%;
  height: 100vh;
  left: 20vw;
  top: 0px;
  box-sizing:border-box;
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 30px;

}


.dayand{
  width:100%;
  bottom: 8vh;
  position:absolute;
  display :flex;
  padding-left: 5vw;
}
  .ymd { 
    font-size: 36px;
    color: #343a40;
    text-align: left;
    font-weight: bold;
    margin-top: 8vh;
    vertical-align: bottom;
  }
  .day {
    color: #868e96;
    font-size: 21px;
    margin-left: 1vw;
    margin-top: 11vh;
    vertical-align: bottom;
  }

  .tasks-left {
    color: #20c997;
    font-size: 16px;
    font-weight: bold;
    bottom: 4.5vh;
    position: absolute;
    padding-left: 5vw;
    text-align: left;
  }


  .header{
    position: relative;
    width: 100%;
    height: 25vh;
    font-family: Roboto;
    font-style: normal;
    display: flex;
    align-items: flex-start;
    color: #000000;
  }
  .list{
    margin-top: 1vw;
    flex: 1;
    overflow: scroll;
    width: 73vw;
    height: 65vh;
    padding: 1.5vh 3vw;
    overflow-x:hidden; 
    overflow-y:auto; 
    position: relative;
  }

  .list::-webkit-scrollbar{
    width: 0.5vw;
}

/* 스크롤바 막대 설정*/
.list::-webkit-scrollbar-thumb{
    height: 17%;
    background-color: rgba(33,133,133,1);
    border-radius: 10px;  
}

/* 스크롤바 뒷 배경 설정*/
.list::-webkit-scrollbar-track{
    background-color: rgba(33,133,133,0.33);
}